@import "./../../../abstracts/vars";
@import "./../../../abstracts/mixins";

.newForm {
  @include flex-edit(center, center);

  .wrap {
    padding: 1.5rem 1.5rem 1.5rem 2.5rem;
    margin: 3.8rem 0 2.6rem;

    width: 25rem;
    height: 34rem;

    display: flex;
    flex-direction: column;
    justify-content: start;

    background-color: $whiteColor;
  }

  .title {
    margin: 2rem 0 4rem;

    @include text-edit(Poppins, 600, 1.4rem, 0.7);
  }

  .passwordBox {
    margin-top: 1.5rem;

    display: flex;
    flex-direction: column;

    position: relative;

    .btnShow {
      width: 24px;
      height: 24px;

      position: absolute;
      top: 15px;
      right: 13px;

      cursor: pointer;

      &:hover {
        font-weight: 400;
        text-shadow: none;
        box-shadow: none;
      }

      border: none;
      background-color: transparent;

    }

    .input::placeholder {
      @include text-edit(SF UI Display, 500, 0.8rem, 1.5);

      color: #24254A;

      opacity: 0.5;
    }
  }

  .textDiscr {
    margin-top: 1.8rem;

    @include text-edit(SF UI Display, 500, 1rem, 1.5);

    color: $lightGrey;

    text-align: center;
  }

  .btnCreate {
    margin-top: 2rem;
    width: 266px;
    height: 36px;

    align-self: center;
    margin-top: 5.6rem;

    color: $whiteColor;
  }
}